<template>
  <div class="highlight-wrap">
    <Description
      :option="option"
      @removeOption="removeOption"
    />

    <div class="">
      <ExtentionRowItem :row-label="`${$t('tips.scope')}`">
        <Select
          v-model="formData.scope"
          @on-change="handleChange"
        >
          <Option
            v-for="option in scopeOptions"
            :key="option.value"
            :value="option.value"
          >
            {{ option.label }}
          </Option>
        </Select>
      </ExtentionRowItem>

      <div class=" highlight-rules">
        <div class="form-group">
          <div class="tw-mb-10">
            <span>{{$t('extensionProperty.value')}}&nbsp;&nbsp;&nbsp;：</span>
            <i-switch
              v-model="formData.value"
              @on-change="valueChange"
            />
          </div>
          <template v-if="formData.value">
            <span class="tw-mt-10">{{$t('tips.rule')}}：</span>
            <Poptip
              trigger="hover"
              content="content"
            >
              <Icon
                type="ios-help-circle-outline"
                class="help"
              />
              <div slot="content" class="highlight-rules-wrap">
                <div class="title">用法示例：</div>
                <div>
                  不等于空字符串: v!==''
                </div>
                <div>
                  等于字符串'啊': v==='啊'
                </div>
                <div>
                  大于数字0: v > 0
                </div>
                <div>
                  0到100之间: v >= 0 &amp;&amp; v &lt;= 100
                </div>
                <div>
                  小于0或者大于100:  v&lt;0 || v>100
                </div>
              </div>
            </Poptip>
            <ExtentionRowItem
              v-for="(rule,index) in formData.rules"
              :key="index"
              :row-index="index"
              :row-count="formData.rules.length"
              show-operation-button
              @addRow="addRow(formData.rules, RULE)"
              @removeRow="removeRow(formData.rules, index)"
            >
              <div class="col">
                <Input
                  :placeholder="$t('extensionProperty.expressionPlaceholder')"
                  v-model.trim="rule.operator"
                  clearable
                />
              </div>
              <div class="col">
                <Poptip
                  trigger="click"
                  content="content"
                >
                  <div
                    class="style-button"
                    :style="rule.style"
                  >{{$t('extensionProperty.setStyle')}}</div>
                  <div slot="content">
                    <div class="style-item">
                      <div>{{$t('tips.color')}}:</div>
                      <div><input
                          type="color"
                          v-model="rule.style.color"
                        ></div>
                    </div>
                    <div class="tw-mt-4 style-item">
                      <div>{{$t('tips.backgroundColor')}}:</div>
                      <div><input
                          type="color"
                          v-model="rule.style.backgroundColor"
                        ></div>
                    </div>
                    <div class="tw-mt-4 style-item">
                      <div>{{$t('tips.textDecoration')}}:</div>
                      <div>
                        <Select v-model="rule.style.textDecoration">
                          <Option
                            v-for="option in decorationOptions"
                            :key="option.value"
                            :value="option.value"
                          >
                            {{ option.label }}
                          </Option>
                        </Select>
                      </div>
                    </div>
                    <div class="tw-mt-4 style-item">
                      <div>{{$t('tips.fontWeight')}}:</div>
                      <div>
                        <Select v-model="rule.style.fontWeight">
                          <Option
                            v-for="option in weightOptions"
                            :key="option.value"
                            :value="option.value"
                          >
                            {{ option.label }}
                          </Option>
                        </Select>
                      </div>
                    </div>
                    <div class="tw-mt-4 style-item">
                      <div>{{$t('tips.fontStyle')}}:</div>
                      <div>
                        <Select v-model="rule.style.fontStyle">
                          <Option
                            v-for="option in fontStyleOptions"
                            :key="option.value"
                            :value="option.value"
                          >
                            {{ option.label }}
                          </Option>
                        </Select>
                      </div>
                    </div>
                  </div>
                </Poptip>
              </div>
            </ExtentionRowItem>
          </template>
        </div>

        <div class="tw-mt-10 form-group">
          <span>{{$t('tips.label')}}：</span>
          <i-switch
            v-model="formData.label"
            :disabled="formData.scope === 'table'"
            @on-change="labelChange"
          />
          <template v-if="formData.label">
            <div class="tw-mt-10">
              <Poptip
                trigger="click"
                content="content"
              >
                <div
                  class="style-button"
                  :style="formData.labelStyle"
                >{{$t('extensionProperty.setStyle')}}</div>
                <div slot="content">
                  <div class="style-item">
                    <div>{{$t('tips.color')}}:</div>
                    <div><input
                        type="color"
                        v-model="formData.labelStyle.color"
                      ></div>
                  </div>
                  <div class="tw-mt-4 style-item">
                    <div>{{$t('tips.backgroundColor')}}:</div>
                    <div><input
                        type="color"
                        v-model="formData.labelStyle.backgroundColor"
                      ></div>
                  </div>
                  <div class="tw-mt-4 style-item">
                    <div>{{$t('tips.textDecoration')}}:</div>
                    <div>
                      <Select v-model="formData.labelStyle.textDecoration">
                        <Option
                          v-for="option in decorationOptions"
                          :key="option.value"
                          :value="option.value"
                        >
                          {{ option.label }}
                        </Option>
                      </Select>
                    </div>
                  </div>
                  <div class="tw-mt-4 style-item">
                    <div>{{$t('tips.fontWeight')}}:</div>
                    <div>
                      <Select v-model="formData.labelStyle.fontWeight">
                        <Option
                          v-for="option in weightOptions"
                          :key="option.value"
                          :value="option.value"
                        >
                          {{ option.label }}
                        </Option>
                      </Select>
                    </div>
                  </div>
                  <div class="tw-mt-4 style-item">
                    <div>{{$t('tips.fontStyle')}}:</div>
                    <div>
                      <Select v-model="formData.labelStyle.fontStyle">
                        <Option
                          v-for="option in fontStyleOptions"
                          :key="option.value"
                          :value="option.value"
                        >
                          {{ option.label }}
                        </Option>
                      </Select>
                    </div>
                  </div>
                </div>
              </Poptip>
            </div>
          </template>

        </div>
      </div>

    </div>
  </div>
</template>

<script type="text/ecmascript-6">
import deepClone from '../../../utils/deepClone';
import { debounce } from '../../../utils/common';
import commonExtension from '../mixins/common-extension'

const RULE = {
  operator: '',
  style: {
    fontWeight: 'normal', // normal,bold
    fontStyle: 'normal', // normal,italic
    textDecoration: 'none', // none, underline, line-through
    backgroundColor: '#ffffff',
    color: '#000000'
  }
};

export default {
  name: 'Highlight',

  mixins: [commonExtension],

  data() {
    return {
      formData: {
        scope: 'global',
        // type: '',
        rules: [deepClone(RULE)],
        label: false,
        value: false,
        labelStyle: {
          fontWeight: 'normal', // normal,bold
          fontStyle: 'normal', // normal,italic
          textDecoration: 'none', // none, underline, line-through
          backgroundColor: '#ffffff',
          color: '#000000'
        }
      },
      scopeOptions: [
        {
          value: 'form',
          label: '表单'
        },
        {
          value: 'table',
          label: '列表'
        },
        {
          value: 'global',
          label: '全局'
        }
      ],
      // typeOptions1: [
      //   {
      //     value: 'value',
      //     label: '值'
      //   },
      //   {
      //     value: 'label',
      //     label: '标签'
      //   },
      //   {
      //     value: 'all',
      //     label: '值和标签'
      //   }
      // ],
      // typeOptions2: [
      //   {
      //     value: 'value',
      //     label: '值'
      //   },
      // ],
      weightOptions: [
        {
          value: 'normal',
          label: '正常'
        },
        {
          value: 'bold',
          label: '加粗'
        },
      ],
      fontStyleOptions: [
        {
          value: 'normal',
          label: '正常'
        },
        {
          value: 'italic',
          label: '斜体'
        },
      ],
      decorationOptions: [
        {
          value: 'none',
          label: '无装饰'
        },
        {
          value: 'underline',
          label: '下划线'
        },
        {
          value: 'line-through',
          label: '删除线'
        },
      ],
      RULE: deepClone(RULE)
    }
  },

  watch: {
    formData: {
      handler(newValue) {
        const newFormData = this.filterForm(newValue)
        if (newFormData === '') {
          this.$emit('dataChange', { key: this.option.key, value: '' })
        } else {
          this.$emit('dataChange', { key: this.option.key, value: newFormData })
        }
      },
      deep: true
    },
  },

  methods: {
    valueChange() {
      this.formData.rules = [deepClone(RULE)]
    },

    labelChange() {
      this.formData.labelStyle = {
        fontWeight: 'normal', // normal,bold
        fontStyle: 'normal', // normal,italic
        textDecoration: 'none', // none, underline, line-through
        backgroundColor: '#ffffff',
        color: '#000000'
      }
    },

    handleChange() {
      if (this.formData.scope === 'table') {
        this.formData.label = false
        this.formData.labelStyle = {
          fontWeight: 'normal', // normal,bold
          fontStyle: 'normal', // normal,italic
          textDecoration: 'none', // none, underline, line-through
          backgroundColor: '#ffffff',
          color: '#000000'
        }
      }
    },
    // 筛选出表单有效值
    // 作用域、类型和规则必须都存在，且规则三个选项都有值才算有效(样式必须修改过)
    filterForm(oldData) {
      const data = deepClone(oldData)
      for (let i = data.rules.length - 1; i >= 0; i--) {
        const item = data.rules[i]
        if (!item.operator) {
          data.rules.splice(i, 1)
          continue
        }
        // 如果样式和初始值相同，说明没有修改过样式，视为无效条件
        if (JSON.stringify(item.style) === JSON.stringify(deepClone(RULE.style))) {
          data.rules.splice(i, 1)
          continue
        }

        // 背景颜色默认#fff，字体颜色默认#000，值没变也认为没修改值
        if (item.style.color === '#000000') {
          item.style.color = ''
        }
        if (item.style.backgroundColor === '#ffffff') {
          item.style.backgroundColor = ''
        }
      }
      const valueValid = data.value && data.rules.length !== 0 // 值区域是否有效
      const labelValid = data.scope !== 'table' && data.label && JSON.stringify(data.labelStyle) !== JSON.stringify(deepClone(RULE.style)) // 标签是否有效
      // 值和标签区域都没有有效值时
      if (!valueValid && !labelValid) {
        return ''
      }

      // 背景颜色默认#fff，字体颜色默认#000，值没变也认为没修改值
      if (data.labelStyle.color === '#000000') {
        data.labelStyle.color = ''
      }
      if (data.labelStyle.backgroundColor === '#ffffff') {
        data.labelStyle.backgroundColor = ''
      }
      return data
    },

    // 清空配置项
    removeOption(keyArray) {
      Object.assign(this.$data, this.$options.data.call(this))
      this.$emit('removeOption', keyArray || [])
    },

    // 初始化数据
    initData() {
      if (!this.defaultData) {
        return
      }
      const formData = deepClone(this.defaultData)
      this.formData = formData
      this.formData.rules.forEach(rule => {
        if (rule.style.color === '') {
          rule.style.color = '#000000'
        }
        if (rule.style.backgroundColor === '') {
          rule.style.backgroundColor = '#ffffff'
        }
      })
      if (this.formData.labelStyle.color === '') {
        this.formData.labelStyle.color = '#000000'
      }
      if (this.formData.labelStyle.backgroundColor === '') {
        this.formData.labelStyle.backgroundColor = '#ffffff'
      }
    },
  },

  mounted() {
    this.initData();
  }
}
</script>

