<template>
  <div class="RowToColumn">
    <Description
      :option="option"
      @removeOption="removeOption"
    />

    <div
      class="form-group"
      v-if="isInited"
    >
      <ExtentionRowItem
        :row-label="`${$t('extensionProperty.sourceTable')}`"
        :errorTip="tableErrorTip"
      >
        <div class="col">
          <DropDownSelectFilter
            single
            :placeholder="$t('tips.pleaseSelect')"
            :data="dropTableList"
            :auto-data="searchTableList"
            :page-size="pageSize"
            :total-row-count="totalTableCount"
            :columns-key="columnsTable"
            :hidecolumns="hidecolumns"
            :default-selected="defaultForm.table ? defaultForm.table:undefined"
            @on-popper-show="getTableData"
            @on-page-change="getTableData"
            @on-input-value-change="inputTableData"
            @on-fkrp-selected="selectValue($event, formData, 'table', 'ID')"
            @on-clear="clearValue(formData, 'table')"
          />
        </div>
      </ExtentionRowItem>

      <ExtentionRowItem
        :row-label="`${$t('tips.find')}${$t('extensionProperty.field')}`"
        :errorTip="fieldErrorTip"
      >
        <div class="col">
          <DropDownSelectFilter
            ref="fieldDrop"
            single
            :placeholder="$t('tips.pleaseSelect')"
            :data="dropList"
            :auto-data="searchList"
            :page-size="pageSize"
            :total-row-count="totalCount"
            :columns-key="columnsKey"
            :hidecolumns="hidecolumns"
            :default-selected="defaultForm.refcolumn ? defaultForm.refcolumn:undefined"
            @on-popper-show="getSearchData"
            @on-page-change="getSearchData"
            @on-input-value-change="inputSearchData"
            @on-fkrp-selected="selectValue($event, formData, 'refcolumn', 'ID')"
            @on-clear="clearValue(formData, 'refcolumn')"
          />
        </div>
      </ExtentionRowItem>

      <ExtentionRowItem
        :row-label="`${$t('tips.find')}${$t('extensionProperty.condition')}`"
        :errorTip="conditionErrorTip"
      >
        <div class="col">
          <Input
            v-model="formData.value"
            :placeholder="$t('extensionProperty.enterValue')"
          />
        </div>
      </ExtentionRowItem>
    </div>
  </div>
</template>

<script type="text/ecmascript-6">
import i18n from '../../../utils/i18n'
import deepClone from '../../../utils/deepClone';
import { debounce } from '../../../utils/common';
import commonExtension from '../mixins/common-extension'

export default {
  name: 'RowToColumn',

  mixins: [commonExtension],

  data() {
    return {
      formData: {
        table: '',
        refcolumn: '',
        value: ''
      },
      columnsTable: ['DESCRIPTION'],
      dropTableList: {},
      searchTableList: [],
      totalTableCount: 0,
      defaultForm: {},
      isInited: false, // 是否初始化过了
      tableErrorTip: '',
      fieldErrorTip: '',
      conditionErrorTip: '',
    }
  },

  watch: {
    formData: {
      handler(newValue) {
        if (!newValue.table) {
          this.formData.refcolumn = ''
          this.defaultForm.refcolumn = undefined
          this.$refs.fieldDrop.inputValue = ''
        }
        this.checkValue()

        const newFormData = this.filterForm(newValue);
        if (newFormData === '') {
          this.$emit('dataChange', { key: this.option.key, value: '' });
        } else {
          this.$emit('dataChange', { key: this.option.key, value: newFormData });
        }
      },
      deep: true
    },
  },

  methods: {
    // 清空配置项
    async removeOption(keyArray) {
      // 清楚整个配置数据
      Object.assign(this.$data, this.$options.data.call(this))
      this.$emit('removeOption', keyArray || [])
      this.isInited = true
    },

    filterForm(newValue) {
      if (newValue.table && newValue.refcolumn && newValue.value) {
        return deepClone(newValue)
      }
      return ''
    },

    // 校验值
    checkValue() {
      if (this.formData.table) {
        this.tableErrorTip = ''
      } else {
        this.tableErrorTip = `${this.$t('tips.pleaseSelect')}${this.$t('extensionProperty.sourceTable')}`
      }

      if (this.formData.refcolumn) {
        this.fieldErrorTip = ''
      } else {
        this.fieldErrorTip = `${this.$t('tips.pleaseSelect')}${this.$t('tips.find')}${this.$t('extensionProperty.field')}`
      }

      if (this.formData.value) {
        this.conditionErrorTip = ''
      } else {
        this.conditionErrorTip = `${this.$t('extensionProperty.enterValue')}${this.$t('tips.find')}${this.$t('extensionProperty.condition')}`
      }
    },

    // 查询表
    async getTableData(page) {
      let startindex = 0;
      if (typeof page === 'number') {
        startindex = (page - 1) * this.pageSize;
      }

      const searchdata = {
        table: 'AD_TABLE',
        startindex,
        range: 10,
        fixedcolumns: {
          ISACTIVE: ['=Y'],
        },
        column_include_uicontroller: true,
        isolr: false
      };

      this.dropTableList = await this.getTableList(searchdata);
      this.totalTableCount = this.dropTableList.totalRowCount;
    },

    // 请求表接口
    async getTableList(searchdata) {
      return new Promise((resolve) => {
        const hideColumns = ['ORDERNO', 'MASK', 'URL', 'AD_OBJUICONF_ID', 'AD_VERSION_ID', 'CUSTOMIZENO', 'DISP_COLUMN_ID', 'FILTER', 'ISACTIVE', 'ISDISPATCHABLE', 'ISTREE', 'REALTABLE_ID', 'PROPS', 'PROC_SUBMIT', 'MODIFIERNAME', 'MODIFIEDDATE'];
        const api = '/p/cs/QueryList';
        const result = this.commonApi(api, { searchdata }, {
          hideColumns,
          displayKeys: ['显示名称'],
        });
        resolve(result);
      });
    },

    // 输入表名时进行模糊查询
    inputTableData: debounce(function (value) {
      if (value === '') {
        this.searchTableList = [];
        return;
      }

      const searchdata = {
        table: 'AD_TABLE',
        startindex: 0,
        range: 10,
        fixedcolumns: {
          ISACTIVE: ['=Y'],
          DESCRIPTION: value,
        },
        column_include_uicontroller: true,
        isolr: false
      };


      this.getTableList(searchdata).then((data) => {
        const result = data.row || [];

        // 格式化好了，将字段值转大写与输入值对比，进行手动过滤
        this.searchTableList = result.map((keyObj) => {
          Object.keys(keyObj).forEach((key) => {
            keyObj[key] = keyObj[key].val;
          });
          return keyObj;
        });

        this.totalTableCount = this.dropTableList.totalRowCount;
      });
    }),

    // 查询字段
    async getSearchData(page) {
      let startindex = 0;
      if (typeof page === 'number') {
        startindex = (page - 1) * this.pageSize;
      }

      const tableId = this.formData.table
      const searchdata = {
        table: 'AD_COLUMN',
        startindex,
        range: 10,
        fixedcolumns: {
          ISACTIVE: ['=Y'],
          AD_TABLE_ID: [tableId],
        },
        column_include_uicontroller: true,
        isolr: false
      };
      if (!tableId) {
        delete searchdata.fixedcolumns.AD_TABLE_ID;
      }

      this.dropList = await this.getKeyList(searchdata);
      this.totalCount = this.dropList.totalRowCount;
    },

    // 请求字段接口
    async getKeyList(searchdata) {
      return new Promise((resolve) => {
        const hideColumns = ['ORDERNO', 'MASK', 'AD_TABLE_ID', 'AD_VERSION_ID', 'ISORDER', 'ISACTIVE', 'ISAGFILTER', 'AGFILTER', 'ISINDEXED', 'NAME', 'OBTAINMANNER', 'REF_COLUMN_ID', 'FKDISPLAY', 'SEARCHMODEL', 'ISREMOTE', 'AD_LIMITVALUE_GROUP_ID', 'DISPLAYTYPE', 'COMMENTSTP', 'MODIFIERID', 'MODIFIEDDATE'];
        const api = '/p/cs/QueryList';
        const result = this.commonApi(api, { searchdata }, {
          hideColumns,
          displayKeys: ['表内名称'],
        });
        resolve(result);
      });
    },

    // 输入字段名时进行模糊查询
    inputSearchData: debounce(function (value) {
      if (value === '') {
        this.searchList = [];
        return;
      }

      const tableId = this.formData.table
      const searchdata = {
        table: 'AD_COLUMN',
        startindex: 0,
        range: 10,
        fixedcolumns: {
          ISACTIVE: ['=Y'],
          DBNAME: value,
          AD_TABLE_ID: [tableId],
        },
        column_include_uicontroller: true,
        isolr: false
      };
      if (!tableId) {
        delete searchdata.fixedcolumns.AD_TABLE_ID;
      }

      this.getKeyList(searchdata).then((data) => {
        const result = data.row || [];

        // 格式化好了，将字段值转大写与输入值对比，进行手动过滤
        this.searchList = result.map((keyObj) => {
          Object.keys(keyObj).forEach((key) => {
            keyObj[key] = keyObj[key].val;
          });
          return keyObj;
        });

        this.totalCount = this.dropList.totalRowCount;
      });
    }),

    // 初始化
    async init() {
      if (!this.defaultData) {
        this.isInited = true
        return
      }

      this.formData = deepClone(this.defaultData)

      // 回填来源表
      let searchdata = {
        table: 'AD_TABLE',
        startindex: 0,
        range: 10,
        fixedcolumns: {
          ISACTIVE: ['=Y'],
          ID: this.formData.table
        },
        column_include_uicontroller: true,
        isolr: false
      };

      const tableResult = (await this.getKeyList(searchdata)).row || [];
      if (tableResult.length > 0) {
        this.defaultForm.table = [{
          Label: tableResult[0].DESCRIPTION.val,
          ID: this.formData.table,
        }]
      }
      // 回填查询字段
      searchdata = {
        table: 'AD_COLUMN',
        startindex: 0,
        range: 10,
        fixedcolumns: {
          ISACTIVE: ['=Y'],
          AD_TABLE_ID: this.formData.table,
          ID: this.formData.refcolumn
        },
        column_include_uicontroller: true,
        isolr: false
      };

      const fieldResult = (await this.getTableList(searchdata)).row || [];
      if (fieldResult.length > 0) {
        this.defaultForm.refcolumn = [{
          Label: fieldResult[0].DBNAME.val,
          ID: this.formData.refcolumn,
        }]
      }

      this.isInited = true
    }
  },

  created() {
    this.init()
  },

  beforeCreate() {
    this.$t = i18n.t.bind(i18n)
  },
}
</script>

<style lang="less">
.RowToColumn {
  .row-title {
    flex: 80px 0 0;
  }
}
</style>